#TWENTYTWENTYTWO {
    font-size: 5em;
    overflow: hidden;
    color: white;
    font-family: 'Iceland';
    line-height: 5rem;
    width: 100%;
    max-height: 5rem;
    max-width: fit-content;

    /* animation */
    border-right: .15em solid rgb(105, 39, 39);
    animation: 
      typing 3.5s steps(16, end),
      cursorblink 0.8s infinite;
      animation-iteration-count: infinite;  
}
  
@keyframes typing {
    from { width: 0 }
    to { width: 120% }
}
  
@keyframes cursorblink {
    from { border-color: transparent }
    to { border-color: rgb(105, 39, 39); }
}

